.app{text-align:center;background:url("../assets/background.png") no-repeat center center fixed;background-size:cover;font-family:"Poppins",sans-serif;font-weight:500}.logo{position:absolute;top:-50px;right:0px}.title{color:#f2f2f2;text-align:left;font-weight:700}.questions-wrapper{background-color:#fff;padding:50px 20px;border-radius:24px}.questions-wrapper .title-question{font-size:24px;font-weight:700;line-height:36px;color:#2f527b;text-align:left}.questions-wrapper .flag{display:block;margin:10px 0;border:.3px solid #d3d3d3}.questions-wrapper ul{margin:0;padding:0}.questions-wrapper ul .option{display:flex;align-items:center;color:#6066d0;border:2px solid #878cdc;color:#878cdc;font-size:18px;border-radius:15px;margin:10px 0;padding:10px 2px;transition:.3s}.questions-wrapper ul .option p{margin:0;margin-left:10px;vertical-align:middle;text-align:left}.questions-wrapper ul .option p:nth-child(1){font-size:24px}.questions-wrapper ul .option.hover:hover{background-color:#f9a826;color:#fff;border:2px solid #fff}.questions-wrapper ul .option.correct{background-color:#60bf88;color:#fff;border:2px solid #fff}.questions-wrapper ul .option.correct::after{content:"";font-family:"bootstrap-icons";flex-grow:1;margin-right:15px;text-align:right;font-size:20px}.questions-wrapper ul .option.incorrect{background-color:#ea8282;color:#fff;border:2px solid #fff}.questions-wrapper ul .option.incorrect::after{content:"";font-family:"bootstrap-icons";flex-grow:1;margin-right:15px;text-align:right;font-size:20px}.questions-wrapper .buttons-container{width:100%;display:flex;justify-content:flex-end}.questions-wrapper .buttons-container .button{cursor:pointer;background-color:#f9a826;color:#fff;border:none;border-radius:12px;font-weight:700;font-size:18px;line-height:27px;padding:10px 20px}.result-wrapper{background-color:#fff;padding:50px 20px;border-radius:24px}.result-wrapper h1{font-weight:700;font-size:48px;line-height:72px;color:#1d355d}.result-wrapper p{color:#1d355d;font-weight:400;font-size:18px;line-height:27px}.result-wrapper p span{color:#60bf88;font-size:36px;font-weight:400}.result-wrapper button{border:2px solid #1d355d;border-radius:12px;font-size:18px;line-height:27px;padding:10px 30px}.footer{color:#f2f2f2}.footer-name{text-decoration:underline;font-weight:700}.footer-link{color:#f2f2f2;text-decoration:none}.footer-link:hover{color:#f2f2f2}